<script>
    import { mapGetters, mapActions } from 'vuex'
    import io from 'socket.io-client'
    const socket = io('http://localhost:1307')

    export default {
        computed: {
          ...mapGetters([
              'signInned',
              'auth'
          ])
        },
        data: () => ({
            clipped: false,
            drawer: false,
            fixed: false,
            miniVariant: false,
            categories: []
        }),
        methods: {
            ...mapActions([
                'filterCategory'
            ]),
          async fetchCategories () {
             const results = await this.$axios.get('category/getall')
              this.categories = results.data.categories
          },
            filterCate (id) {
                this.$store.commit('SetCATEGORY', id)
                this.filterCategory(1)
                this.drawer = false
            }
        },
        mounted () {
                socket.on('message', (msg) => {
                    if(msg.to === this.auth._id) {
                        this.$swal({
                            title: msg.message,
                            toast: true,
                            position: 'top-end',
                            showConfirmButton: false,
                            timer: 3000
                        })
                    }
                })
        },
        created () {
            this.fetchCategories()
        }
    }
</script>
